<template>
    <el-dilaog 
    :visible.sync="dlgVisible">
        <el-carousel :initial-index="currentIndex">
            <el-carousel-item v-for="(url, i) in imgUrls" :key="i" height="200px">
                <img :src="url" alt="">
            </el-carousel-item>
        </el-carousel>
    </el-dilaog>
</template>

<script>
export default {
    name: 'userbook-img-dlg',
    props: {
        currentIndex: {
            type: Number, 
            default: '',
            required: true,
        }, 
        imgUrls: {
            type: Array, 
            default: '',
        }, 
        visible: {
            type: Boolean, 
            default: false,
        }
    }, 
    data() {
        return {
            dlgVisible: false
        }
    },
    created() {
        this.dlgVisible = this.visible;
    }, 
    watch: {
        dlgVisible(val){
            this.$emit('update:visible', val);
        }
    }
}
</script>
